.LandingContainer {
  margin: 0;
  padding: 0;
  position: relative;
  min-width: 100%;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  overflow: hidden;
}

.LandingVideoContainer {
  position: absolute;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  z-index: -1;
}
@media (min-width: 320px) {
  /* smartphones, Android phones, landscape iPhone */
  .LandingVideoContainer {
    left: calc(50% - (320px * 2));
  }
}
@media (min-width: 480px) {
  /* smartphones, Android phones, landscape iPhone */
  .LandingVideoContainer {
    left: calc(50% - (480px * 2));
  }
}
@media (min-width: 600px) {
  /* portrait tablets, portrait iPad, e-readers (Nook/Kindle), landscape 800x480 phones (Android) */
  .LandingVideoContainer {
    left: calc(50% - 600px);
  }
}
@media (min-width: 801px) {
  /* tablet, landscape iPad, lo-res laptops ands desktops */
  .LandingVideoContainer {
    left: 0;
  }
}

.LandingTextContainer {
  color: #fff;
  -ms-flex-align: center;
      align-items: center;
  padding: 4%;
}
.LandingTitle {
  text-align: center;
  margin: 0;
  padding: 0;
  font-size: 2em;
  font-family: "Permanent Marker", cursive;
}
.LandingSubTitle {
  text-align: center;
  font-size: 1.5em;
  font-family: "Caveat Brush", cursive;
}
.LandingBtnAction {
  text-align: center;
}
.LandingBtn {
  margin: 1em;
  padding: 1em 2em;
  border-radius: 50px;
  text-decoration: none;
  color: rgb(216, 216, 216);
  background: rgb(131, 58, 180);
  -webkit-box-shadow: -4px 6px 8px -4px rgba(0, 0, 0, 0.3);
          box-shadow: -4px 6px 8px -4px rgba(0, 0, 0, 0.3);
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(131, 58, 180, 1)),
    to(rgba(252, 176, 69, 1))
  );
  background: -webkit-linear-gradient(
    left,
    rgba(131, 58, 180, 1) 0%,
    rgba(252, 176, 69, 1) 100%
  );
  background: -o-linear-gradient(
    left,
    rgba(131, 58, 180, 1) 0%,
    rgba(252, 176, 69, 1) 100%
  );
  background: linear-gradient(
    90deg,
    rgba(131, 58, 180, 1) 0%,
    rgba(252, 176, 69, 1) 100%
  );
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}
.LandingBtn:hover {
  color: white;
  -webkit-box-shadow: -6px 8px 10px -1px rgba(0, 0, 0, 0.7);
          box-shadow: -6px 8px 10px -1px rgba(0, 0, 0, 0.7);
}

@media (min-width: 600px) {
  /* portrait tablets, portrait iPad, e-readers (Nook/Kindle), landscape 800x480 phones (Android) */
}
@media (min-width: 801px) {
  /* tablet, landscape iPad, lo-res laptops ands desktops */
  .LandingTitle {
    font-size: 3.5em;
  }
  .LandingSubTitle {
    font-size: 2em;
  }
  .LandingTextContainer {
    padding: 0;
  }
}
@media (min-width: 1281px) {
  /* hi-res laptops and desktops */
  .LandingTitle {
    font-size: 4em;
  }
  .LandingSubTitle {
    font-size: 2.5em;
  }
  .LandingTextContainer {
    padding: 0;
  }
}



